export default {
	onLoad() {
		this.COMMONFUNCTION.getStorageByKey('selectedCoupon').then(data=>{
			this.coupon=data;
			this.COMMONFUNCTION.removeStorageByKey('selectedCoupon')
		})
	},
	data() {
		return {
			coupon:{}
		};
	}
}
